.AppAbout{
    background: no-repeat fixed url("https://raw.githubusercontent.com/ArmillaMain/Armilla/main/Images/Background3.png");
    background-color: blueviolet;
    background-size: cover;
    min-height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
    color: white;
    font-family: 'Poppins', sans-serif;
}

.AppAbout section{
    position: relative;
    justify-content: center;
    display: flex;
    flex-direction: row;
    flex: 0 0 33%;
    flex-wrap: wrap;
    gap: 20px;
    background-color: rgba(0, 0, 0, 0.349);
    min-width: 80%;
    height: 80%;
    border-radius: 20px;
    padding: 20px;
    padding-top: 40px;
    padding-bottom: 40px;
    margin-top: 80px;
    margin-bottom: 80px;
}

.part{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    text-align: center;
    /*background-color: rgba(51, 51, 51, 0.123);
    border: solid 1px rgb(255, 0, 0);*/
    max-width: calc(33% - 20px);
    min-width: 300px;
}
    .part i{
        font-size: 50px;
        margin-bottom: 20px;
    }

